.content_cover {
    width: 100vw;
    height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.content_cover svg {
    fill: rgb(97, 97, 97);
    width: 10vh;
    height: 10vh;
}

.content_cover p {
    margin: 0px;
    font-size: 3vh;
    font-weight: 200;
    padding: 0 10vw;
}

.contents {
    opacity: 0;
    -webkit-animation-name: fadeIn;
    animation-name: fadeIn;
    -webkit-transform-origin: center bottom;
    -ms-transform-origin: center bottom;
    transform-origin: center bottom;
    -webkit-animation-duration: 3.3s;
    animation-duration: 3.3s;
    -webkit-animation-fill-mode: both;
    animation-fill-mode: both;
    -webkit-animation-iteration-count: 1;
    animation-iteration-count: 1;
}

@-webkit-keyframes fadeIn {
    0% {
        opacity: 0;
    }
    100% {
        opacity: 1;
    }
}

@keyframes fadeIn {
    0% {
        opacity: 0;
    }
    100% {
        opacity: 1;
    }
}

.content_container {
    width: 100vw !important;
    height: 100vh !important;
}

.content_sider {
    overflow-y: scroll;
    overflow-x: hidden;
    text-overflow: ellipsis;
    height: 100vh;
}

.header {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    height: 100%;
    align-items: center;
    overflow: hidden;
    min-width: fit-content;
    max-width: 100vw;
    margin: 0px;
}

.header>* {
    margin: 0 20px;
}

.header .svg {
    fill: pink;
    transition: all .3s ease-in-out;
    cursor: pointer;
}

.header svg {
    height: 30px;
    width: 30px;
}

.header .svg:hover {
    fill: rgb(219, 137, 151);
}

.header h1 {
    margin: 0px;
    color: pink;
    font-size: 30px;
    font-weight: lighter;
    max-width: 50vw;
    overflow: hidden;
    text-overflow: ellipsis;
}